body{
    background-color:cornsilk;
}

h1{
    color: green;
}

.success{
    color: green;
    background: lightgrey;
    font-size: 20px;
    border-style: dashed;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

.error{
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}